.card{
    position: relative;
    background-color: white;
    width:600px;
    padding: 15px;
    max-width: 85vw;
    height: 50vh;
    box-shadow: 15px 13px 30px -11px rgba(0,0,0,0.3);
    border-radius: 20px;
    background-size: cover;
    background-position: center;
}

.tinderCards-cardContainer{
    display: flex;
    justify-content: center;
    margin-top:10vh;
}

.cardContent{
    width:100%;
    height:100%;
    /* object-fit: contain; */
}

.swipe{
    position: absolute;
}

.card h3{
    position: absolute;
    bottom:0;
    margin:10px;
    color:#fff;
} 
